<style scoped>
	.layout{
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
}
.layout-logo{
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
}
.layout-nav{
    width: 500px;
    margin: 0 auto;
    margin-right: 20px;
}
.routerStyle {
  font-weight: bold;
  color: #FFFFFF;
}
.routerStyle.router-link-exact-active {
  color: #F0AD4E;
}
.routerStyle2 {
	font-weight: bold;
	color: #000000;
}
.routerStyle2.router-link-exact-active {
  color: #F0AD4E;
}
.backwhite{
	background-color: #FFFFFF;
}
</style>
<template>
	<div class="layout">
		<Layout>
			<Header>
				<div>
					<font color="#FFFFFF" size="5">Linkage</font>
				</div>
			</Header>
			<Layout>
				<Sider hide-trigger :style="{background: '#fff'}">
					<Menu 
					ref='menu'
					:active-name="activeName" 
					theme="light" 
					width="auto" 
					:open-names="['1','2','3','4']" 
					v-if="islog"
					@on-select='select'>
						<Submenu name="1">
							<template slot="title">
								<Icon type="md-cafe"></Icon>
								Moments
							</template>
							<MenuItem name="1-1" @click.native='shareMoments()'>Share Your Moment</MenuItem>
							<MenuItem name="1-2" @click.native='browseMoments()'>Browse Others</MenuItem>
						</Submenu>
						<Submenu name="4">
							<template slot="title">
								<Icon type="logo-chrome"></Icon>
								Posts
							</template>
							<MenuItem name="4-1">Post Article</MenuItem>
							<MenuItem name="4-2">Browse Others</MenuItem>
						</Submenu>
						<Submenu name="2">
							<template slot="title">
								<Icon type="md-notifications"></Icon>
								Message
							</template>
							<MenuItem name="2-1" @click.native='invitation()'>Invitation</MenuItem>
							<MenuItem name="2-2" @click.native='chat()'>Chat</MenuItem>
						</Submenu>
						<Submenu name="3">
							<template slot="title">
								<Icon type="md-contact"></Icon>
								Me
							</template>
							<MenuItem name="3-1" @click.native='showAccount()'>
							Account
							</MenuItem>
							<MenuItem name="3-2" @click.native='showFriends()'>
							Friends
							</MenuItem>
							<MenuItem name="3-3" @click.native='showSubscription()'>
							Subscription
							</MenuItem>
						</Submenu>
					</Menu>
				</Sider>
				<Layout :style="{padding: '0 24px 24px'}">
					<!-- <keep-alive>
						<router-view @child-event='parentEvent'></router-view>
					</keep-alive> -->
					<router-view @child-event='parentEvent' @changeActiveName='changeActiveName'></router-view>
				</Layout>
			</Layout>
		</Layout>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				islog: true,
				activeName: '3-1'
			}
		},
		mounted(){
			
		},
		methods: {
			select(name){
				this.activeName = name
			},
			changeActiveName(activeName) {
				console.log('App is accepting emit ...')
				this.activeName = activeName
				console.log(this.activeName)
				this.$nextTick(() => {
					console.log('executin nextTick ...')
					this.$refs.menu.updateOpened();
					this.$refs.menu.updateActiveName();
				})
			},
			parentEvent(data) {
				if (data == 'LogSuccess')
					this.islog = true;
			},
			shareMoments: function() {
				this.$router.push('shareMoments');
			},
			browseMoments: function() {
				this.$router.push('browseMoments');
			},
			showAccount: function() {
				this.$router.push('account');
			},
			showFriends: function() {
				this.$router.push({
					path:'friends',
					name:'friends',
					params:{
						currentPage:1
					}
				});
			},
			showHome: function() {
				this.$router.push('home');
			},
			showMessage: function() {
				this.$router.push('message');
			},

			chat: function() {
				this.$router.push('chat');
			},
			invitation: function() {
				this.$router.push('invitation');
			},
			showSubscription: function() {
				this.$router.push({
					path:'subscription',
					name:'subscription',
					params:{
						currentPage:1
					}
				});
			}
		}
	}
</script>
